.dialogWrap {
	display: flex;
	width: 100%;
	height: 90px;
	cursor: pointer;
	padding-right: 30px;
	background-color: salmon;
	border: 1px solid grey;
	transition: all 0.5s linear;
	-moz-box-shadow: 2px 2px 4px 0px #006773;
	-webkit-box-shadow: 2px 2px 4px 0px #006773;
}

.dialogWrap:hover,
.dialogWrap:active,
.dialogWrap:focus {
	box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.4);
	transform: scale(1.02);
}

.dialogWrap:hover {
	background-color: green;
}

.dialogWrap:active {
	background-color: yellow;
}

a {
	text-decoration: none;
}

.textContent {
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 100%;
	padding-left: 22px;
	margin-top: 4px;
	padding-bottom: 12px;
	word-break: break-word;
	text-overflow: clip;
}

.dialogName {
	width: 100%;
	height: 40%;
	text-align: left;
	font-size: large;
	color: yellow;
	margin-top: 10px;
}

.dialogAvatar {
	margin: 10px 0 0 10px;
	border-radius: 50px;
	background-size: cover;
	width: 70px;
	min-width: 70px;
	height: 70px;
	background: url('../images/Ava1.jpg') no-repeat;
	background-size: cover;
}

.status {
	float: right;
	height: 100%;
	width: 20%;
	display: flex;
	flex-direction: column;
}

.messageTime {
	width: 100%;
	height: 40%;
	margin-left: 65%;
	font-size: small;
	padding: 15px;
	color: black;
}

.lastMessage {
	text-align: left;
	width: 100%;
	height: 60%;
	color: wheat;
}

.messageStatus {
	width: 100%;
	height: 60%;
	margin-left: 65%;
	margin-right: 0;
	padding: 10px;
	color: #0074d9;
}
