::-webkit-scrollbar {
  width: 0px;
}

.messageWrap {
  padding-top: 60px;
  padding-bottom: 50px;
  list-style: none;
  width: 100%;
  height: 100%;
  background-size: 50px;
  overflow: hidden;
  overflow-y: scroll;
}

.messageBox {
  box-sizing: border-box;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-bottom: 100px;
  list-style: none;
  float: right;
  word-break: break-all;
  width: 100%;
  padding: 0 10px 0 10px;
  animation-name: smoothDrop;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.self {
}

.outSide {
  width: 100%;
  max-width: 40%;
}

.dropZone {
  width: 100%;
  height: 100%;
  margin: 15px;
  padding: 20px;
  background: gray;
  opacity: 0.2;
  border-radius: 10px;
  border: 10px solid black;
}
